body {
    background-color: black;
    margin: 0;
}
 .header {
    background-color: #8685EF;
    color:#1c1c1c;
    font-family: 'Montserrat';
    font-weight: bold;
    font-size: large;
    text-align: center;
    height: 1.5em;
    padding-top: 0.5em;
 }
.global {
    border-radius: 5px;
    background-color: #1c1c1c;

    box-shadow: 34px 37px 23px -37px rgba(134,133,239,1);
    margin:1em 1.3em;
    color:white;
    font-family: 'Montserrat';
    display:flex;
    gap:1em;
    margin-bottom: 1em;;
}

.global--imagecontainer {
    /* border:2px solid red; */
    width:35%;
}

.global--info {
    /* border:2px solid green; */
    width:60%;
    position: relative;
}

.global--info > p {
    font-size: 0.5rem;
}

.global--info > h3 {
    padding-top: 1em;
    padding-bottom: 0.4em;
    font-size: 0.8rem;
    margin-top: 0;
    margin-bottom: 0;
}

.global--info > h4 {
    font-size: 0.6rem;
    margin-top: 0;
}

.global--info--link {
    text-align: right;
    text-decoration: none;
    color: #1c1c1c;
    font-weight: bold;
    font-size: 0.6rem;
    background-color: #8685EF;
    padding: 0.2em 0.2em;
    border-radius: 4px;
    position: absolute;
    left:16em;
    top:16.6em;

}

.global--image {
    border-radius: 5px;
    /* border: 2px solid magenta; */
    width: 100%;
    height: 180px;
    object-fit: cover;
}

.copyright{
    color:white;
    text-align: center;
    font-family: 'Montserrat';
}

@media screen and (min-width:768px) {

    .deskview {
        display: grid;
        grid-template-rows: 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr;
        row-gap: 0.7em;
        column-gap: 0px;
        margin-top: 2em;
        margin-left: 2em;
        
    }

    .global {
        width: 26em;
        margin: 0;
        margin-left: 0;
        margin-right: 0;
    }

    .global--info > p {
        font-size: 0.7rem;
    }
    
    .global--info > h3 {
        padding-top: 1em;
        padding-bottom: 0.4em;
        font-size: 1rem;
        margin-top: 0;
        margin-bottom: 0;
    }
    
    .global--info > h4 {
        font-size: 0.6rem;
        margin-top: 0;
    }
    
    .global--info--link {
        text-align: right;
        text-decoration: none;
        color: #1c1c1c;
        font-weight: bold;
        font-size: 0.8rem;
        background-color: #8685EF;
        padding: 0.2em 0.2em;
        border-radius: 4px;
        position: absolute;
        left:13em;
        top:3em;
    
    }

    .global--image {
        border-radius: 5px;
        /* border: 2px solid magenta; */
        width: 100%;
        height: 180px;
        object-fit: cover;
        padding-top: 1.2em;
    }
}